<template>
  <div class="addressWrap">
      <header>
        <router-link to="profileinfo">
            <i class="el-icon-arrow-left"></i>
        </router-link>
        <div>编辑地址</div>
        <span class="s1" v-if="isShow" @click="change">编辑</span>
        <span class="s1" v-if="!isShow" @click="change1">完成</span>
    </header>
    <section class="address">
        <ul class="addresslist">
            <li v-for="(a,index) in addressMsg" :key="index">
                <div>
                    <p>{{a.name}}</p>
                    <p>{{a.phone}}</p>
                </div>
                <div class="deletesite" v-if="!isShow">
                    <span @click="deleteAdd(index,a.id)">x</span>
                </div>
            </li>
        </ul>
        <router-link to="/addaddress">
            <div class="addsite">
                <span>新增地址</span>
                <span class="addsvg">
                    <i class="el-icon-arrow-right"></i>
                </span>
            </div>
        </router-link>
    </section>
  </div>
</template>

<script>
export default {
  name:'Address',
  data(){
      return{
          addressMsg:[],
          isShow:true,
          userid:'',
          addressid:''
      }
  },
  created(){
    //   this.addressMsg = JSON.parse(localStorage.getItem("addressMsg")) 
    this.userid = localStorage.getItem('userid')
    this.axios.get("/v1/users/"+this.userid+"/addresses",{
            params:{
                user_id:this.userid,
            }
        }).then(res=>{
            console.log(res)
            this.addressMsg = res;
        }).catch(err=>{})
  },
  methods:{
      change(){
          this.isShow = false
      },
      change1(){
          this.isShow = true
      },
      deleteAdd(num,id){
            this.axios.delete("/v1/users/"+this.userid+"/addresses/"+id,{
            params:{
                user_id:this.userid,
                address_id:id
            }
            }).then(res=>{
            // console.log(res);
            this.axios.get("/v1/users/"+this.userid+"/addresses",{
            params:{
                user_id:this.userid,
            }
        }).then(res=>{
            // console.log(res)
            this.addressMsg = res;
        }).catch(err=>{})
            }).catch(err=>{})
      }
  }
}
</script>

<style scoped>
.addressWrap{
    padding-top: 1.95rem;
}
a{
    text-decoration: none;
}
header>a{
  position: absolute;
  left: 0.4rem;
  color: #fff;
  font-size: 0.65rem;
  top: 50%;
  transform: translateY(-50%);
  text-decoration: none;
}
header>div{
    width: 50%;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
    width: 50%;
    color: #fff;
    text-align: center;
    font-size: .8rem;
    font-weight: 700;
}
.s1{
    right: .4rem;
    font-size: .7rem;
    color: #fff;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
}
.addsite{
    margin-top: .4rem;
    background: #fff;
    padding: .2rem .4rem;
    border-top: 1px solid #d9d9d9;
    display: flex;
    justify-content: space-between;
}
.address{
    width: 100%;
    margin-top: .4rem;
    border-top: 1px solid #d9d9d9;
    border-bottom: 1px solid #d9d9d9;
}
.addresslist{
    background-color: #fff;
}
.addresslist>*:first-child{
    background: #fff8c3;
}
.addresslist li{
    border-bottom: 1px solid #d9d9d9;
    padding: .4rem;
    display: flex;
    justify-content: space-between;
}
.addresslist li p{
    line-height: .9rem;
    font-size: .6rem;
    color: #333;
}
.addsite span{
    display: block;
    font-size: .7rem;
    color: #333;
    line-height: 1.4rem;
}
.addsvg{
    width: .66667rem;
    height: 1.4rem;
}
.deletesite{
    display: flex;
    align-items: center;
}
.deletesite span{
    display: block;
    font-size: .8rem;
    color: #999;
}
</style>